//2. interface -> 1. íƒ€ì…ì„ ê²°í•©ì‹œí‚¤ê±°ë‚˜ ì¡°í•©ì‹œí‚¤ëŠ” ë°©ì‹ì´ ë‹¤ë¦„ ( A extends B ) 2. ìœ ë‹ˆì˜¨ íƒ€ì…ì€ ì¸í„°í˜ì´ìŠ¤ë¥¼ ì‚¬ìš© ë¶ˆê°€ëŠ¥
interface Store {
    currentPage: number;
    feeds: NewsFeed[];
}

interface News {
    readonly id: number; // ì½”ë“œì—ì„œ id ìˆ˜ì • ë¶ˆê°€ëŠ¥ ( ì„œë²„ì— idê°’ì´ ìˆê¸°ë•Œë¬¸ì— ë°”ë€Œë©´ í˜¸ì¶œí•˜ì§€ ëª»í•  ìˆ˜ë„ ìˆëŠ” ê²½ìš° )
    readonly time_ago: string;
    readonly title: string;
    readonly url: string;
    readonly user: string;
    readonly content: string;
}

interface NewsFeed extends News {
    readonly comments_count: number;
    readonly points: number;
    read?: boolean; // ?: -> optional 
}

interface NewsDetail extends News {
    readonly comments: [];
}

interface NewsComment extends News {
    readonly comments: [];
    readonly level: number;
}

const container: HTMLElement | null = document.getElementById('root');
const ajax: XMLHttpRequest = new XMLHttpRequest();
const NEWS_URL = 'https://api.hnpwa.com/v0/news/1.json';
const CONTENT_URL = 'https://api.hnpwa.com/v0/item/@id.json'; // @id ë°”ê¿”ì¹˜í‚¤

const content = document.createElement('div');
//const ul = document.createElement('ul');

let endFlag;

const store: Store = {
    currentPage : 1, // ìµœê·¼ í˜ì´ì§€
    feeds : [],
}; 

class Api {
    url: string;
    ajax: XMLHttpRequest;
    
    constructor(url: string){
        this.url = url;
        this.ajax = new XMLHttpRequest();
    }

    protected getRequest<AjaxResponse>(): AjaxResponse { // protected ìƒì„±ì ì•ˆì˜ í•¨ìˆ˜ë¡œ ë°”ê¹¥ì—ì„œ í˜¸ì¶œë˜ì§€ì•Šê²Œ
        this.ajax.open('GET', this.url, false);
        this.ajax.send();
        return JSON.parse(this.ajax.response);
    }

}

class NewsFeedApi extends Api{
    getData(): NewsFeed[] {
        return this.getRequest<NewsFeed[]>();
    }
}

class NewsDetailApi extends Api{
    getData(): NewsDetail {
        return this.getRequest<NewsDetail>();
    }
}

