function makeFeeds(feeds: NewsFeed[]): NewsFeed[]{
    for(let i = 0; i < feeds.length; i++){
        feeds[i].read = false; 
    }

    return feeds;
}

function updateView(html: string): void{
    if(container != null){
        container.innerHTML = html;
    }else{
        console.error('ìµœìƒìœ„ ì»¨í…Œì´ë„ˆê°€ ì—†ì–´ UIë¥¼ ì§„í–‰í•˜ì§€ ëª»í•©ë‹ˆë‹¤.');
    }  

}

function newsFeed(): void{
    const api = new NewsFeedApi(NEWS_URL);
    let newsFeed: NewsFeed[] = store.feeds;
    const newsList = [];
    let template = `
        <div class="bg-gray-600 min-h-screen">
            <div class="bg-white text-xl">
                <div class="mx-auto px-4">
                    <div class="flex justify-between items-center py-6">
                        <div class="flex justify-start">
                            <h1 class="font-extrabold">Hacker News</h1>
                        </div>
                        <div class="items-center justify-end">
                            <a href="#/page/{{__prev_page__}}" class="text-gray-500">
                                Previous
                            </a>
                            <a href="#/page/{{__next_page__}}" class="text-gray-500 ml-4">
                                Next
                            </a>
                        </div>
                    </div>
                </div>
            </div>
            <div class="p4 text-2xl text-gray-700">
                {{__news_feed__}}
            </div>
        </div>
    `; // handlebars templates ì ìš©í•´ë³´ê¸°

    if(newsFeed.length === 0){
       newsFeed = store.feeds = makeFeeds(api.getData()); // ë‘ì¤„ì§œë¦¬ ì½”ë“œ í•œì¤„ë¡œ
    }
    // for ë¬¸ ë‚´ë¶€ i ëŠ” íƒ€ì… ì¶”ë¡ ìœ¼ë¡œ ìë™ìœ¼ë¡œ numberí˜•ìœ¼ë¡œ ì¸ì‹ 
    for(let i = (store.currentPage - 1) * 10; i < store.currentPage * 10; i++){
        //const div = document.createElement('div');
        

        newsList.push(`
            <div class="p-6 ${newsFeed[i].read ? 'bg-red-500' : 'bg-white'} mt-6 rounded-lg shadow-md transition-colors duration-500 hober:bg-green-100">
                <div class="flex">
                    <div class="flex-auto">
                        <a href = #/show/${newsFeed[i].id}>${newsFeed[i].title}</a>
                    </div>
                    <div>
                        <div class="text-center text-sm">${newsFeed[i].comments_count}</div>
                    </div>
                </div>
                <div class="flex mt-3">
                    <div class="grid grid-cols-3 text-sm text-gray-500">
                        <div><i class="fas fa-user mr-1"></i>${newsFeed[i].user}</div>
                        <div><i class="fas fa-heart mr-1"></i>${newsFeed[i].points}</div>
                        <div><i class="far fa-clock mr-1"></i>${newsFeed[i].time_ago}</div>
                    </div>
                </div>
            </div>
        `);
    }

    
    template = template.replace('{{__news_feed__}}', newsList.join('')); //container.innerHTML = newsList.join(''); // ì¡°ì¸ ì‚¬ìš©ì‹œ ë°°ì—´ì•ˆì˜ ë¬¸ìì—´ë“¤ì„ í•©ì³ì„œ ë°˜í™˜í•´ì¤€ë‹¤, ë°°ì—´ ì¸ë±ìŠ¤ ì‚¬ì´ì˜ êµ¬ë¶„ì(default = ,)ê°€ ì¡´ì¬í•˜ê¸°ë•Œë¬¸ì— ì—†ì• ì¤˜ì•¼í•˜ëŠ”ë°,, join í•¨ìˆ˜ì˜ ì²«ë²ˆì§¸ íŒŒë¼ë¯¸í„°ëŠ” êµ¬ë¶„ìë¥¼ ì–´ë–¤ê²ƒì„ ì‚¬ìš©í• ì§€ ì •í•˜ëŠ” ë¶€ë¶„. ê·¸ë ‡ê¸°ë•Œë¬¸ì— '' ë¼ëŠ” ê³µë°±ì„ ë„£ìœ¼ë©´ ë¬¸ìì—´ë§Œì„ ë°˜í™˜í•´ì¤€ë‹¤.
    template = template.replace('{{__prev_page__}}', String(store.currentPage > 1 ? store.currentPage -1 : 1));
    template = template.replace('{{__next_page__}}', String(store.currentPage + 1));

    updateView(template);
    
}


function newsDetail(): void{
    //console.log(location.hash); //idê°€ì ¸ì˜¤ê¸° (ë¸Œë¼ìš°ì €ê°€ ê¸°ë³¸ìœ¼ë¡œ ì œê³µí•´ì£¼ëŠ” ê°ì²´)
    const id = location.hash.substr(7);
    const api = new NewsDetailApi(CONTENT_URL.replace('@id', id));
    const newsContent = api.getData();
    let template = `
        <div class="bg-gray-600 min-h-screen pb-8">
            <div class="bg-white text-xl">
                <div class="mx-auto px-4">
                    <div class="flex justify-between items-center py-6">
                        <div class="flex justify-start">
                            <h1 class="font-extrabold">Hacker News</h1>
                        </div>
                        <div class="items-center justify-end">
                            <a href="#/page/${store.currentPage}" class="text-gray-500">
                                <i class="fa fa-times"></i>
                            </a>
                        </div>
                    </div>
                </div>
            </div>
            <div class="h-full border rounded-xl bg-white m-6 b-4">
                <h2>${newsContent.title}</h2>
                <div class="text-gray-400 h-20">
                    ${newsContent.content}
                </div>

                {{__comments__}}

            </div>
        </div>
    `;

    for(let i = 0; i < store.feeds.length; i++){
        if (store.feeds[i].id === Number(id)){
            store.feeds[i].read = true;
            break;
        }
    }
  
    updateView(template.replace('{{__comments__}}', makeComment(newsContent.comments)));
}

function makeComment(comments: NewsComment[]): string{
    const commentString =[];

    for(let i = 0; i < comments.length; i++){
        const comment: NewsComment = comments[i];
        commentString.push(`
            <div style="padding-left : ${comment.level * 40}px;" class="mt-4">
                <div class="text-gray-400">
                    <i class="fa fa-sort-up mr-2"></i>
                    <strong>${comment.user}</strong> ${comment.time_ago}
                </div>
                <p class="text-gray-700">${comment.content}</p>
            </div>
        `);
        if(comment.comments.length > 0){
            commentString.push(makeComment(comment.comments));
        }
    }

    return commentString.join('');
}

function router(): void{
    const routePath = location.hash; 

    if(routePath === ''){// location í•´ì‰¬ì— #ì´ ë“¤ì–´ì˜¤ë©´ ë¹ˆ ê°’ ë°˜í™˜ì´ ë˜ì–´ ì¡°ê±´ë¬¸ì´ ë™ì‘í•˜ëŠ” ê²ƒ
        newsFeed();
    } else if (routePath.indexOf('#/page/') >= 0) { // ë¬¸ìì—´ ê°’ ì•ˆì— ì¡´ì¬í•˜ë©´ ìœ„ì¹˜ì •ë³´, ì—†ìœ¼ë©´ -1
        store.currentPage = Number(routePath.substr(7));
        newsFeed();
    } else {
        newsDetail();
    }
}

window.addEventListener('hashchange', router);

router();

